export default {
  data: function () {
    return {
      back: require('../assets/back.png'),
      name: '我的周报',
      items: [
        {
          title: '第一周  我的标题'
        }
      ]
    }
  },
  methods: {
    onClick: function (it) {
      this.$router.push({
        name: 'WeekDetails',
        params: {
          object: it
        }
      })
    }
  }
}



